<template>
	<div id="coupon-page">
		<edit-header :headerTitle="headerTitle">
			<span class="edit" slot="edit" @click="editClick">添加优惠券</span>
		</edit-header>

		<div class="coupon-tab">
			<span :class="{active:1==tabIndex}" @click="changeTab(1)">未使用</span>
			<span :class="{active:2==tabIndex}" @click="changeTab(2)">已使用</span>
			<span :class="{active:3==tabIndex}" @click="changeTab(3)">已过期</span>
		</div>

		<div class="coupon-wrap" :class="{'coupon-wrap-type':$route.name=='ConfirmCoupon'}">
			<coupon v-if="!isEmpty" :tabIndex = 'tabIndex' :couponList='couponList' v-on:loadMore="loadMore"></coupon>
			<div v-if="isEmpty" style="text-align: center;">暂无数据</div>
		</div>
		
		<div class="coupon-btn" v-if="$route.name=='ConfirmCoupon'">确定</div>
	</div>
</template>

<script>
let params = {
	pa :1,
	li:10,
	ob:1,
	ot:1,
	status:1
}	
	
import Coupon from '@/components/Coupon/Coupon'
import EditHeader from '@/components/Header/EditHeader'
	
export default {
	data(){
		return {
			headerTitle:'优惠券',
			tabIndex:1,
			isEmpty:false,
			params:{
				pa :1,
				li:10,
				ob:1,
				ot:1,
				status:1
			},
			couponList:[
//			{id:1,name:'优惠券名称，最多显示两行优惠券名称，最多显示两行',cardNum:'BA3489489G984GH',isvalid:1,status:1,totalAmount:100,startTime:'2018-1-10 00:00:00',EndTime:'2018-3-10 15:30:00'},
//			{id:2,name:'优惠券名称，最多显示两行优惠券名称，最多显示两行',cardNum:'BA3489489G984GH',isvalid:1,status:1,totalAmount:100,startTime:'2018-1-10 00:00:00',EndTime:'2018-3-19 10:00:00'},
//			{id:2,name:'优惠券名称，最多显示两行优惠券名称，最多显示两行',cardNum:'BA3489489G984GH',isvalid:1,status:1,totalAmount:100,startTime:'2018-1-10 00:00:00',EndTime:'2018-3-19 10:00:00'},
//			{id:2,name:'优惠券名称，最多显示两行优惠券名称，最多显示两行',cardNum:'BA3489489G984GH',isvalid:1,status:1,totalAmount:100,startTime:'2018-1-10 00:00:00',EndTime:'2018-3-19 10:00:00'},
//			{id:2,name:'优惠券名称，最多显示两行优惠券名称，最多显示两行',cardNum:'BA3489489G984GH',isvalid:1,status:1,totalAmount:100,startTime:'2018-1-10 00:00:00',EndTime:'2018-3-19 10:00:00'},
//			{id:2,name:'优惠券名称，最多显示两行优惠券名称，最多显示两行',cardNum:'BA3489489G984GH',isvalid:1,status:1,totalAmount:100,startTime:'2018-1-10 00:00:00',EndTime:'2018-3-19 10:00:00'},
			],
		}
	},
  components: {
  	Coupon,
  	EditHeader
  },
  created(){
	this.getCouponList(1);
  },
  watch:{
    '$route' (to, from) {
  		
    }
  },
  methods:{
  	editClick(){
  		this.$router.push({name:'AddCoupon'})
  	},
  	changeTab(index){
  		this.tabIndex = index;
  		this.couponList = []
			this.getCouponList(index)
  	},
  	loadMore(){
  		console.log('loadMore')
//			this.couponList.push({id:2,name:'优惠券名称，最多显示两行优惠券名称，最多显示两行',cardNum:'BA3489489G984GH',isvalid:1,status:1,totalAmount:100,startTime:'2018-1-10 00:00:00',EndTime:'2018-3-19 10:00:00'})
  	},
  	getCouponList(index){
  		this.$set(this.params, 'status', index)
  		this.$Api.getCouponList(this.params).then((res)=>{
  			console.log(res)
  			if(res.q.s==0){
  				if(res.q.total==0 && params.pa==1){
  					this.isEmpty = true
  				}else{
  					this.couponList = res.q.coupons
  					this.isEmpty = false
  				}
  			}
  		})
  	},
  },
  
}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#coupon-page{
		height: 100%;
		background: #f0f0f0;
		.coupon-tab{
			position:fixed;
			z-index:100;
			width: 100%;
			height: 1.07rem;
			line-height: 1.07rem;
			top: 1.17rem;
			display: flex;
			font-size: 0.4rem;
			border-bottom: 1px solid $border-color;
			background: #fff;

			>span{
				flex: 1;
				text-align: center;
			}
			.active{
				color: $label-color;
				border-bottom: 2px solid $label-color;
			}
		}
		.coupon-wrap{
			position: absolute;
			top: 1.49+1.17rem;
			width: 100%;
		}
		.coupon-wrap-type{
			bottom: 1.58rem;
			overflow: auto;
		}
		.coupon-btn{
			position: fixed;
			bottom: 0;
			font-size: 0.45rem;
			width: 100%;
			height: 1.31rem;
			line-height: 1.31rem;
			color: #fff;
			z-index: 100;
			text-align: center;
			@include bg-color;
		}
	}
</style>


