<template>
	<div id="coupon">
		<div class="coupon-list">
			<!--<load-more :bottom-method="loadMore" :bottom-all-loaded="allLoaded" :bottom-status.sync="bottomStatus" >-->
			<ul>
				<li class="coupon-item" v-for="(item,index) in couponList">
					<div class="coupon-price" :class="[tabIndex==1?'enable':'unable']">
						<span class="price"><em>￥</em>{{item.totalAmount}}</span>
					</div>
					<div class="coupon-info">
						<p class="coupon-name">{{item.name}}</p>
						<p class="coupon-code">{{item.cardNum}}</p>
					</div>
					<div class="coupon-date">
						<div class="check-box">
							<input v-if="tabIndex==1 && $route.name =='ConfirmCoupon'" class="checkbox" type="checkbox" name="" id="" value="" />
							<div v-if="tabIndex!=3">剩<em>{{countDay(item.startTime,item.EndTime)}}</em>{{dateStr}}</div>
							<div v-if="tabIndex==3">已过期</div>
						</div>
					</div>
					<img class="used-icon" v-if="tabIndex==2" src="../../assets/common_icon_used@2x.png"/>
				</li>
			</ul>
			<!--</load-more>-->
			<!--<div class="loading-wrap">
				<img class="load-icon" src="data:image/gif;base64,"/>
				<span>努力加载</span>
			</div>-->
			<!--<load-more :loading="showLoad"></load-more>-->
		</div>
	</div>
</template>

<script>
	
export default {
	props:['tabIndex','couponList'],
	data(){
		return {
			showLoad:false,
			allLoaded:false,
			loading:'',
			bottomStatus:'',
		}
	},
	computed:{
		
	},
  components: {
  	
  },
  created(){
  	
  },
  methods:{
  	couponInit(){
  		let name = this.$route.name
  		if(name == 'ConfirmCoupon'){
  			
  		}else if(name == 'Coupon'){
  			
  		}
  	},
  	countDay(starTime,endTime){
  		let dayStr=''
			let start = starTime
			let end = endTime
			
			let nowdate = new Date();
	    let nowtime = nowdate.getTime();
	    let endtime = new Date(end).getTime();
	    
	    let begintime_ms = Date.parse(new Date(start.replace(/-/g, "/"))); //begintime 为开始时间
			let endtime_ms = Date.parse(new Date(end.replace(/-/g, "/")));   // endtime 为结束时间
	    
	    if(nowtime>=begintime_ms){
	    	begintime_ms = nowtime
	    }
	          
	    let differ =  endtime_ms - begintime_ms;
	    let days=Math.floor(differ/(24*3600*1000));
	    //计算出小时数
			let leave1=differ%(24*3600*1000)    //计算天数后剩余的毫秒数
			let hours=Math.floor(leave1/(3600*1000))
			//计算相差分钟数
			let leave2=leave1%(3600*1000)        //计算小时数后剩余的毫秒数
			let minutes=Math.floor(leave2/(60*1000))
			
			if(nowtime-endtime>=0){
				return;
			}else{
				if(days>0){
					dayStr = days
					this.dateStr = '天'
				}else if(days<=0 && hours>0){
					dayStr = hours
					this.dateStr = '小时'
				}else if(days<=0 && hours<=0 && minutes>0){
					dayStr = minutes
					this.dateStr = '分钟'
				}
			}
			return dayStr
		},
		loadMore() {
		  this.loading = true;
		  this.showLoad = true;
		  this.$emit('loadMore');
//		  setTimeout(() => {
//		    this.loading = false;
//		  	this.showLoad = false;
//		  }, 2500);
		}
  }
  
}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#coupon{
		height: 100%;
		background: #f0f0f0;
		.coupon-item{
			position: relative;
			display: flex;
			margin:0 auto 0.27rem auto;
			width: 9.29rem;
			background: #fff;
			.coupon-price{
				flex:0 0 3.47rem;
				height: 2.67rem;
				line-height: 2.67rem;
				text-align: center;
				color: #fff;
				margin-right: 0.27rem;
				.price{
					font-size: 0.91rem;
					>em{
						font-size: 0.56rem;
					}
				}
			}
			.enable{
				background-color: #fe9bbb;
				background-image: url(../../assets/coupon-side.png);
				background-repeat: no-repeat;
				background-size: 0.13rem 2.67rem;
				background-position-x: 0;
			}
			.unable{
				background-color: #cccccc;
				background-image: url(../../assets/coupon-side2.png);
				background-repeat: no-repeat;
				background-size: 0.13rem 2.67rem;
				background-position-x: 0;
			}
			.coupon-info{
				flex: 1;
				display: flex;
				justify-content:space-around;
				flex-direction:column;
				.coupon-name{
					font-size: 0.37rem;
					@include textflow-two;
					color: #333333;
				}
				.coupon-code{
					font-size: 0.35rem;
					color: #666;
				}
			}
			.coupon-date{
				text-align: center;
				font-size: 0.32rem;
				color: #666;
				.check-box{
					width:1.6rem;
					height: 100%;
					/*height: 2.67rem;*/
					/*line-height: 2.67rem;*/
					display: flex;
					flex-direction:column;
					justify-content: center;
					input[type=checkbox]{
						position: relative;
						left: 50%;
						transform: translateX(-50%);
						width: 0.56rem;
						height: 0.56rem;
				    border: solid 1px #999999;
    				border-radius: 100%;
    				-webkit-appearance: none;
				    -webkit-tap-highlight-color: #000;
				    outline: none;
    				background: #fff;
					}
					input[type=checkbox]:checked:before{
						display: inline-block;
						content: "";
						position: relative;
						left: 50%;
						transform: translateX(-50%);
						width: 0.53rem;
			      height: 0.53rem;
			      background: url("../../assets/cart/ico_checked.png") no-repeat center;
			      background-size: 0.53rem 0.53rem;
			      background-position: 0 -1px;
			      border: none;
					}
					>div{
						text-align: center;
						.van-checkbox__label{
							margin-left: 0;
						}
						>em{
							color: #fe4436;
						}
					}
				}
			}
			.used-icon{
				position: absolute;
				width: 1.87rem;
				height: 1.87rem;
				right: 0;
				top: 0;
			}
		}
		/*.loading-wrap{
			height: 1.17rem;
			line-height: 1.17rem;
			text-align: center;
			font-size: 0.35rem;
			color: #666;
			.load-icon{
				width: 0.4rem;
				height: 0.4rem;
			}
		}*/
	}
</style>


